<template>
  <div class="home">
  	<my-nav></my-nav>
    <div class="header">
      <ul class="my-header clear">
        <li class="icon-t-shirt fl"></li>
        <li class="icon-mail fl"></li>
        <li class="icon-settings fl"></li>
      </ul>
      <div class="my-header-info" v-if="iSLogin">
        <img src="../../assets/image/duoduo.png" alt="">
        <div>
          <router-link to="/login" tag="p">登录</router-link>
          <p>个人信息 &nbsp &nbsp <span>></span></p>
        </div>
      </div>
      <div v-else class="my-header-info">
        <img src="../../assets/image/my/default-avatar.png" alt="">
        <div>
          <p>{{getLoginInfoPass.username}}</p>
          <router-link to='/personalInfo' tag="p" @click.native="toPersonalInfoFn()">个人信息 &nbsp &nbsp <span>></span></router-link>
        </div>
      </div>
    </div>
    <ul class="my-list">
        <li class="icon-about"><span>我的收藏</span></li>
        <li class="icon-foot"><span>我的足迹</span></li>
        <router-link tag="li" to="/myaddress" class="icon-location"><span>收货地址</span></router-link>
        <li class="icon-money"><span>金额</span></li>
        <li class="icon-about"><span>关于</span></li>
      </ul>
  </div>
</template>

<script>
import Nav from '../Nav.vue';
import { mapGetters, mapActions } from 'vuex';
export default {
  data () {
    return {
     iSLogin:true
    }
  },
  computed:{
    ...mapGetters(
      {getLoginInfoArr:'getLoginInfoArr',
       getLoginInfoPass:'getLoginInfoPass' 
      }
      )
  },
  components:{
  	'my-nav':Nav
  },
  methods:{
   toPersonalInfoFn:function(){

   },
   ...mapActions([
        'loginVerify'
      ]),
  },
  mounted:function(){
    if(localStorage.getItem("meituanUser")){
      var str=JSON.parse(localStorage.getItem("meituanUser"))
    if(str.bol==1){
      this.iSLogin=false;
      console.log(str.username)
      this.loginVerify({username:str.username,password:str.password})
    }else{
      this.iSLogin=true
    }
    }




      //为解决
    
    // localStorage.setItem("meituanUser",0);
    // console.log(this.getLoginInfoPass)
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="stylus" scoped>
@import "../../assets/common/stylus/common.styl";
.home
  background #ebebeb
  .header
    width pxtorem(750px)
    height pxtorem(240px)
    background #06c1ae
    position relative
  .my-header
    padding-top pxtorem(30px)
    position absolute
    right pxtorem(50px)
    li
      font-size pxtorem(40px)
      color white
      margin-left pxtorem(30px)
  .my-header-info
    padding pxtorem(80px) 0 0 pxtorem(70px)
    img
      border-radius 50%
      width pxtorem(140px)
      height pxtorem(140px)
    >div
      display inline-block
      vertical-align top
      color white
      font-size pxtorem(30px)
      margin-left pxtorem(40px)
      p
        padding-top pxtorem(20px)
  .my-list
    margin-top pxtorem(32px)
    font-size pxtorem(30px)
    font-weight bolder
    li
      background white
      padding-left pxtorem(50px)
      height pxtorem(90px)
      line-height pxtorem(90px)
      border-bottom 1px solid #ccc;
      span
        vertical-align middle
        margin-left pxtorem(40px)
    li:last-child
      margin-top pxtorem(32px)
    li:before
      display inline-block
      vertical-align middle
      font-size pxtorem(40px)
      
        
</style>



