<template>
  <div class="Address">
  	<commonHeader title="我的收货地址"></commonHeader>
  	<div class="addAddress">
  		<img src="../../assets/image/my/addAddress.png" alt="">
  		<router-link tag="span" to="/editAddress">添加收餐地址</router-link>
  	</div>
  	<ul>
  		 <li v-for="v in addressArr">
       <p><span>{{v.editAddress}}</span><span>{{v.editHouseNum}}</span></p>
       <p><span> {{v.editName}}</span><span>{{v.editSex}}</span><span>{{v.editPhone}}</span></p>
      </li>
  	</ul>
  </div>
</template>

<script>
import commonHeader from './commonHeader'
import { mapGetters, mapActions } from 'vuex';
export default {
  data () {
    return {
    
    }
  },
  components:{
    'commonHeader':commonHeader	
  },
  methods:{
  	
  },
  computed:{
    ...mapGetters({
      addressArr:'getAddressArr'
    })
  },
  mounted:function(){
    console.log(this.addressArr)
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="stylus" scoped>
@import "../../assets/common/stylus/common.styl";
.addAddress
  width pxtorem(750px)
  height pxtorem(100px)
  background white
  text-align center
  line-height pxtorem(100px)
  position fixed
  bottom 0
  left 0
  img
    vertical-align middle
  	width pxtorem(32px)
  	height pxtorem(32px)
  span
  	vertical-align middle
  	color black
  	font-size pxtorem(26px)
ul
  li
    background white
    padding-left pxtorem(30px)
    p:first-child
      padding-top pxtorem(20px)
      font-weight bolder
      font-size pxtorem(26px)
      span:first-child
        margin-right pxtorem(20px)
    p:last-child
      padding-top pxtorem(20px)
      padding-bottom pxtorem(20px)
      font-size pxtorem(28px)
      span:first-child,span:nth-child(2)
        margin-right pxtorem(16px)
</style>



