const IG_BOL = "IG_BOL";
const MOVIE_COMMIT='MOVIE_COMMIT';
const PEOPLE_COMMIT="PEOPLE_COMMIT";
const SENT='SENT';
const CITY = "CITY";
const NUM = "NUM";
const IMAGEDATE="IMAGEDATE";
const insertLoginInfo = "insertLoginInfo";
const loginFn = "loginFn";
const updatePasswordFn="updatePasswordFn";
const setUserLocationFn="setUserLocationFn"
const setAddressArrFn="setAddressArrFn"

export default {
	[IG_BOL] (state,data) {
		state.igbol=data;
	},
	[MOVIE_COMMIT] (state,data) {
		state.movieCommit=data;
	},
	[PEOPLE_COMMIT] (state,data) {
		state.people_commit=data;
	}, 
	[SENT] (state,data) {
		state.sent=data;
	},
	[CITY] (state,data) {
		state.city=data;
	}, 
	[NUM] (state,data) {
		console.log(data)
		state.num=data;
	},
	[IMAGEDATE] (state,data) {
		console.log(data)
		state.imgadata=data;
	},
	[insertLoginInfo](state,data){
		state.loginInfoArr.push(data);
	},
	[loginFn](state,data){
		state.loginInfoPass={}
		state.loginInfoPass=data;
	},
	[updatePasswordFn](state,data){
		state.loginInfoArr[data.index].password=data.rePassword;
	},
	[setUserLocationFn](state,data){
		state.userLocation=data;
	},
	[setAddressArrFn](state,data){
		state.addressArr.push(data)
	}

};


