<template>
  <div class="commonHeader">
  	<img src="../../assets/image/my/back.png" alt="" class="back" @click="backFn()">
  	<div class="header">{{title}}</div>
  </div>
</template>

<script>
export default {
  data () {
    return {
     
    }
  },
  props:["title"],
  methods:{
  	backFn:function(){
  		this.$router.go("-1");
  	}
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="stylus" scoped>
@import "../../assets/common/stylus/common.styl";
.commonHeader
	position relative
.header
	height pxtorem(86px)
	text-align center
	line-height pxtorem(86px)
	font-size pxtorem(30px)
	font-weight 900;background white 
.back
	position absolute
	width pxtorem(40px)
	height pxtorem(40px)
	left pxtorem(30px);
	top pxtorem(26px);

</style>



