<template>
  <div class="home">
    <p class="Head">
      <img src="../../assets/image//剑.png" @click="headFn">
      <span>duoduo美食</span>
    </p>
    <!-- 头部的排序 -->
    <ul v-for="(item,index) in head"  class="content_head">
      <li>
        <span @click="fn(index)">{{item.title}}</span>
        <img v-bind:src="item.url_one" v-show="bol[index]" @click="fn(index)">
        <img v-bind:src="item.url_two" v-show="!bol[index]"  @click="fn(index)">
        <p></p>
      </li>
    </ul>
    <div v-show="show"></div>
    <p class="clear"></p>
    <!-- 内容 -->
    <ul v-for="(item,key) in AllArr" @click="contentFn(key)" class="allArr">
        <li class="single">
           <img v-bind:src="item.pic_url">
           <img v-if="item.poi_type_icon=='' ? false:true" v-bind:src="item.poi_type_icon" >
         </li>
        <li class="last">
          <p>{{item.name}}</p>
          <div>
               <img src="../../assets/image/star.png" v-for="item in snum[key]">
               <img src="../../assets/image/star2.png" v-for="item in snum1[key]">
               <img src="../../assets/image/star1.png" v-for="item in snum2[key]">
          </div>
          <span>月售{{item.month_sale_num}}</span>
          <span>{{item.mt_delivery_time}} | {{item.distance}}</span>
          <p></p>
          <p>{{item.min_price_tip}} | {{item.shipping_fee_tip}}</p>
          <p v-for="teach in item.discounts2" class="foot">
             <img v-bind:src="teach.icon_url">
             <span>{{teach.info}}</span>
          </p>
          <p></p>
        </li>
        <li class="clearp"></li>
        <li class="content_foot"></li>
    </ul>
    <!-- 头部的显示与隐藏 -->
    <ul class="head_sort" v-show="sort_show">
        <li @click="SortFn(index)" v-for="(item,index) in sort" class="sort_Element">{{item}}</li>
    </ul>
    <ul class="select_right" v-show="select_show">
      <li @click="SelectArrk(index)" v-for="(item,index) in SelectAr" class="Selectclass">{{item}}</li> 
    </ul>
       

  </div>
</template>

<script>

import axios from 'axios';
import {mapGetters,mapActions} from "vuex";
import localEvent from '../../store/local';
export default {
  data () {
    return {
       head:[
      {url_one:require("../../assets/image/down.png"),url_two:require("../../assets/image/up.png"),title:"综合排序"},
      {url_one:require("../../assets/image/down.png"),url_two:require("../../assets/image/up.png"),title:"筛选"},
       ],
      score:[],
       snum:[],
       snum1:[],
       snum2:[],
        AllArr:[],
       bol:[true,true],
       show:false,
       sort:["综合排序","速度最快","评分最高","起送价最高","配送价最低"],
       SelectAr:['领优惠卷','商家特色','新商家','品牌商家','清除筛选','完成'],
       newArr:[],
       BolArr:[false,false,false],
       sort_show:false,
       select_show:false,
       livedata:[],
       data:0,
       strs:''
          }
  },
  computed:mapGetters({
      image_data:"getimagedata"
  }),
 created(){
 	var url=localEvent.StorageGetter("url");
     axios.get('http://xlcyq.sc2yun.com/data/data.php',{
        params:{
          file:url
        }}).then(res=>{
      //     if(res.data.data.poilist==""){}else{
      //   localEvent.StorageSetter("select",res);
      // };
      //     var res=localEvent.StorageGetter("select");
          this.AllArr=res.data.data.poilist
           this.livedata=res.data.data.poilist
           // console.log(res.data)
           console.log(this.AllArr[0].discounts2[0])
          // 商家评分
          var arr=[];
          for(var i=0;i<this.AllArr.length;i++){
            arr.push(this.AllArr[i].wm_poi_score)
          }
          this.score=arr;

          this.stars();
          var ss=url.slice(5,url.indexOf('.'));
          this.strs=ss;
          console.log(ss);
     })
  },
  methods:{
  	headFn:function(){
  		  this.$router.go(-1)
  	},
    stars:function (){
        for (var i = 0; i < this.score.length; i++) {
        var b=this.score[i];
        var s = b;
        this.snum[i]=parseInt(s);
        var tt=s-this.snum[i];
        if(tt>=0.5 && tt<=0.99){
         this.snum1[i]=1;
        }else{
         this.snum1[i]=0;
        }
        this.snum2[i]=5-this.snum[i]-this.snum1[i];
      }
    },
    contentFn:function(key){
    	var jq=localEvent.StorageGetter("strs")
    	localEvent.StorageSetter('delies',jq+'0'+(key+1)+".json");
    	this.$router.push({path:'/detail'});
    	console.log(jq+'0'+(key+1)+".json")
    },
    fn:function(index){
         this.bol[index]=!this.bol[index]
         this.show=!this.show
         var head_sort=document.querySelector(".head_sort")
         // var select_right=document.querySelector(".select_right")
         if(this.bol[0]==false){
          this.bol[0]=false
          if(this.select_show==true){
            this.bol[1]=true
            this.select_show=false
          }
           this.sort_show=true
         }
         else if(this.bol[0]==true){
          this.bol[0]=true
          this.sort_show=false
         }  
         if(this.bol[1]==false){
           this.select_show=true
           if(this.sort_show==true){
              this.bol[0]=true
              this.sort_show=false
           }
         }
         else if(this.bol[1]==true){
           this.select_show=false
         }
    },
    sortSmall:function(a,c,d){
            var replace=""
             var replaceArr=this.AllArr
           for(var j=0;j<replaceArr.length;j++){
              for(var i=j;i<replaceArr.length;i++){
              var time_one=Number(replaceArr[i][a].substring(c,d))
              var time_two=Number(replaceArr[j][a].substring(c,d))
                if (time_one<time_two){ 
                     replace=replaceArr[i]
                     replaceArr[i]=replaceArr[j]
                     replaceArr[j]=replace
                }  
             }      
       }
       this.newArr=replaceArr
       this.AllArr=""
       this.AllArr=this.newArr
       this.sort_show=false
       this.bol[0]=true
    },
    sortSmallOpsite:function(a,c,d){
            var replace=""
             var replaceArr=this.AllArr
           for(var j=0;j<replaceArr.length;j++){
              for(var i=j;i<replaceArr.length;i++){
                if(typeof(replaceArr[i][a])=="number"){
                    var time_one=replaceArr[i][a]
                  var time_two=replaceArr[j][a]
                }
                else{
                    var time_one=Number(replaceArr[i][a].substring(c,d))
                var time_two=Number(replaceArr[j][a].substring(c,d))
                }
                if (time_one>time_two){ 
                     replace=replaceArr[i]
                     replaceArr[i]=replaceArr[j]
                     replaceArr[j]=replace
                }  
             }      
       }
       this.newArr=replaceArr
       this.AllArr=""
       this.AllArr=this.newArr
        this.sort_show=false
       this.bol[0]=true

    },
    SortFn:function(index){
         var replace=""
         switch (index) {
           case 0:
             this.AllArr=this.AllArr
             this.sort_show=false
             this.bol[0]=true
             break;
            case 1:  
       this.sortSmall('mt_delivery_time',0,2)
             break; 
             case 2:
       this.sortSmallOpsite('wm_poi_score',0) 
       var arr=[];
         for(var i=0;i<this.AllArr.length;i++){
            arr.push(this.AllArr[i].wm_poi_score)
          }
          this.score=arr;
          this.stars();
             break;
             case 3:
             this.sortSmallOpsite('min_price_tip',4)   
             break;
             case 4:
             this.sortSmall('shipping_fee_tip',4)
             break;
           default:
             break;
         }
    },
       ClassColor:function(data){
        var Selectclass=document.querySelectorAll(".Selectclass")
           Selectclass[data].style.background="#FFFBF1"
           Selectclass[data].style.color="#FFB000";
       },
   SelectArrk:function(index){
        var Selectclass=document.querySelectorAll(".Selectclass")
           switch (index) {
             case 0:
               this.ClassColor(0)
               this.BolArr[0]=true
               break;
               case 2:
              this.ClassColor(2)
                this.BolArr[1]=true
               break;
                case 3:
               this.ClassColor(3)
               this.BolArr[2]=true
               break;
               case 4:
                for(var i=0;i<4;i++){
                  if (i==0||i==2||i==3) {
                      Selectclass[i].style.background=""
                       Selectclass[i].style.color="";
                  };
                  if(i!=3){
                    this.BolArr[i]=false
                  }
                }
                this.AllArr=this.livedata
               break;
               case 5:
                  // this.AllArr=""
                  var selecArr=[]
                  if(this.AllArr.length<20)
                  {
                     this.AllArr=this.livedata
                          } 
                    for(var i=0;i<this.AllArr.length;i++){
                   if(this.BolArr[0]==true&&this.BolArr[1]==true&&this.BolArr[2]==true){
                    if(this.AllArr[i].poi_type_icon!=""&&this.AllArr[i].discounts2[0].icon_url=="http://p0.meituan.net/xianfu/c2c0f31d0ebf0f60af115d058169c492992.png"){
                      selecArr.push(this.AllArr[i]) 
                    }
                  }
                    else if(this.BolArr[0]==true&&this.BolArr[2]==true){
                    	if(this.AllArr[i].discounts2.length<=0){
                  	} 
                  	else{
                  		if(this.AllArr[i].discounts2[0].icon_url=="http://p0.meituan.net/xianfu/c2c0f31d0ebf0f60af115d058169c492992.png"&&this.AllArr[i].poi_type_icon=="http://p0.meituan.net/xianfu/424019d742dbb6b0193ce7ac54a843201104.png")
                      {
                         selecArr.push(this.AllArr[i])
                      }
                  	}
                      
                    }
                    /*这里没有照片*/
                    else if(this.BolArr[0]==true&&this.BolArr[1]==true){
                    	if(this.AllArr[i].discounts2.length<=0){}
                  	else{
                  		if(this.AllArr[i].discounts2[0].icon_url=="http://p0.meituan.net/xianfu/c2c0f31d0ebf0f60af115d058169c492992.png"&&this.AllArr[i].poi_type_icon=="http://p0.meituan.net/xianfu/1d8aa7f1ef0946e602d4ef5317388d831457.png")
                      {
                         selecArr.push(this.AllArr[i])
                      }
                  	}	
                    
                    }
                    else if(this.BolArr[2]==true&&this.BolArr[1]==true){
                      if(this.AllArr[i].poi_type_icon!="" )
                      {
                       selecArr.push(this.AllArr[i])
                     
                       }                         
                  }
                  else if(this.BolArr[0]==true){
                  	if(this.AllArr[i].discounts2.length<=0){
                  	
                  	}else{
                  		if(this.AllArr[i].discounts2[0].icon_url=="http://p0.meituan.net/xianfu/c2c0f31d0ebf0f60af115d058169c492992.png"){
                         selecArr.push(this.AllArr[i])
                      }
                  	}
                      
                  }
                   else if(this.BolArr[1]==true){
                       if(this.AllArr[i].poi_type_icon=="http://p0.meituan.net/xianfu/1d8aa7f1ef0946e602d4ef5317388d831457.png")
                       {
                         selecArr.push(this.AllArr[i])
                       }
                  }
                   else if(this.BolArr[2]==true){
                       if(this.AllArr[i].poi_type_icon=="http://p0.meituan.net/xianfu/424019d742dbb6b0193ce7ac54a843201104.png")
                       {
                          selecArr.push(this.AllArr[i])
                       }
                  } 
                  else if(this.BolArr[0]==false&&this.BolArr[1]==false&&this.BolArr[2]==false)
                   {
                    selecArr.push(this.AllArr[i])
                   }
                  
                   }
                   this.AllArr=""
                   this.AllArr=selecArr
                   this.select_show=false
                   this.bol[1]=true
               break;
             
             default:
               // statements_def
               break;
           }
   }
  
  },
  components:{
  	
  },
  mounted:function(){
  	
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="stylus" scoped>
