          .last>p
               font-size pxtorem(30px)
               font-weight  bold     
              .last>div>img 
                   float left
                   width pxtorem(20px)
                   height pxtorem(20px)
                   margin-top pxtorem(24px)        
          .last>span
               float left
               dispaly block
               width pxtorem(110px)
               margin-top pxtorem(24px)
          .last>span:nth-of-type(2)
               width pxtorem(160px)
               margin-left pxtorem(106px)
          .last>span:nth-of-type(1)
               margin-left pxtorem(23px)     

          .last>p:nth-of-type(2)
               clear both    
          .last>p:nth-of-type(3)
               font-size pxtorem(25px)
               color #666666
               margin-top pxtorem(24px)
               margin-bottom pxtorem(40px)
          .foot
               color #8b8b8b
               font-size pxtorem(15px)
               margin-bottom pxtorem(15px)
               position relative
              .foot>img 
                   width pxtorem(30px)
                   height pxtorem(30px) 
              .foot>span
                   position absolute 
                   top pxtorem(1px)
                   left pxtorem(50px)
                   font-size pxtorem(25px)
                   margin-bottom pxtorem(30px)     
                             
    .clearp
           clear both               
    .content_foot
           width pxtorem(740px) 
           margin-top pxtorem(5px)            
           height pxtorem(1px)
           background gray
           

    .allArr:nth-last-of-type(2)>.content_foot
           background white   
  .div
     width pxtorem(750px)
     height pxtorem(500px)
     background red 
     position absolute 
     top pxtorem(0px)                       
    .head_sort>li
        width pxtorem(750px)
        height pxtorem(80px)
        border-top pxtorem(1px) solid gray
        text-indent pxtorem(30px)
