<template>
<div class="select">
	<div class="fixed">
 		<div class="header">
	 	<img src="../../../assets/image/movie/back.png" @click="backFn">
	 	<p>{{addr.nm}}</p>
	 	</div>
 	</div>
 	<div class="title">
 		<p>{{addr.nm}}</p>
 		<p>{{addr.addr}}</p>
 	</div>
 	<div class="content">
 		<ul>
 			<li></li>
	 		<li ref="css" v-for='(item,index) in movieArr' @click="movieFn(index)">
	 			<img :src="item.img">
	 		</li>
	 		<li></li>
 		</ul>
 	</div>
 	<p class="name">{{name.nm}}  <span>{{name.sc}} 分</span></p>
 	<div class="tiem">
 		 <span :class="bol ? 'sp':'' " @click='dayFn'>今天{{tiem}}</span>
 		 <span :class="!bol ? 'sp':'' " @click="todayFn">明天{{tiem1}}</span>
 	</div>
 	<div class="pric">
 		<div v-for="item in timeArr">
 			<span>{{item}}</span>
	 		<span>国语{{name["3d"] ? "2D" : "3D"}}</span>
	 		<span @click="gochange">选座购票</span>
	 		<span>{{addr.sellPrice}} 元</span>
 		</div>
 	</div>
</div>
</template>

<script>
import axios from 'axios';
import localEvent from '../../../store/local';

export default {
  data () {
    return {
     addr:{},
     allArr:[],
     randomArr:[],
     movieArr:[],
     tiem1:"",
     tiem:"",
     bol:true,
     timeArr:[],
     name:{}
    }
  },
  created(){
  	// 获取位置
  	var addr=localEvent.StorageGetter('addr');
  	this.addr=addr;
  	// 获取电影
  	axios.get('http://xlcyq.sc2yun.com/data/data.php',{
    params: {
      file: 'https://m.maoyan.com/movie/list.json?type=hot&offset=0&limit=1000'}}).then
    (res =>{
    	// if(res.data.data.movies==""){}else{
     //    localEvent.StorageSetter("selectt",res);
     //  };
     //  var res=localEvent.StorageGetter("selectt");
    	var arr=res.data.data.movies;
    	this.allArr=arr;
    	this.randomFn();
    	// 随机抽取10个电影
    	var igarr=[];
    	for(var i=0;i<this.randomArr.length;i++){
    		igarr.push(arr[this.randomArr[i]]);
    	}
    	this.movieArr=igarr;
    	this.name=igarr[0];
    	console.log(this.movieArr);

    	var now = new Date();  
	    var month = now.getMonth() + 1;     //月  
	    var day = now.getDate();            //日
	    this.tiem=month+'月'+day+'日';
	    this.tiem1=month+'月'+(day+1)+'日';
    	// console.log(res.data.data.movies);
    });
    var str=addr.showTimes;
    if(str.indexOf('|'!=-1)){
    	var arr=str.split("|");
    	this.timeArr=arr;
    }else{
    	this.timeArr=[str];
    }
    
  	console.log(addr);

  },
  methods:{
  	gochange:function(){
        this.$router.push({path:'/seat'})
  	},
  	backFn:function(){
  		this.$router.push({path:'/address'})
  	},
  	randomFn:function(){
  	// 随机产生10个数
  	var arr = [];
　　while(arr.length < 10) {//原数组长度为0，每次成功添加一个元素后长度加1，则当数组添加最后一个数字之前长度为9即可
　　　　var num = Math.floor(Math.random() * this.allArr.length);//生成一个0-100的随机整数
　　　　if (arr.length === 0) {//如果数组长度为0则直接添加到arr数组
　　　　　　arr.push(num);
　　　　} else {
　　　　　　for (var i = 0; i < arr.length; i++) {//当新生成的数字与数组中的元素不重合时则添加到arr数组
　　　　　　　　if (arr.join(',').indexOf(num) < 0) {
　　　　　　　　　　arr.push(num);
　　　　　　　　} 
　　　　　　}
　　　　}
　　}
	this.randomArr=arr;
	// console.log(arr);
  	},
  	dayFn:function(){
  		this.bol=true;
  		this.swarr=this.address;
  	},
  	todayFn:function(){
  		this.bol=false;
  		this.swarr=this.address1;
  	},
  	movieFn:function(index){
  		this.name=this.movieArr[index];
  		for(var i=0;i<this.movieArr.length;i++){
  			this.$refs.css[i].style.border=0;
  			this.$refs.css[i].style.transform="scale(1)";
  		}
  		this.$refs.css[index].style.border="8px solid white";
  		this.$refs.css[index].style.transform="scale(1.1)";
  		console.log(this.movieArr[index]);
  	}
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='stylus' scoped>
@import "../../../assets/common/stylus/common.styl"
.select
	background white
	.fixed
		width 100%
		position fixed
		top 0
		left 0
		z-index 100
		.header
			overflow hidden
			position relative
			top 0
			left 0
			background white
			height pxtorem(77px)
			padding-top pxtorem(25px)
			border-bottom pxtorem(2px) solid #ccc
			img
				width pxtorem(36px)
				height pxtorem(43px)
				float left
				margin-left pxtorem(17px)
			p
				text-align center
				font-size pxtorem(39px)
				margin-top pxtorem(5px)
				overflow hidden
				white-space nowrap
				text-overflow ellipsis
	.title
		padding-top pxtorem(136px)
		padding-left pxtorem(25px)
		padding-bottom pxtorem(31px)
		p:nth-child(1)
			font-size pxtorem(31px)
			margin-bottom pxtorem(24px)
		p:nth-child(2)
			font-size pxtorem(28px)
			color gray
	.content
		width pxtorem(750px)
		overflow scroll
		overflow-y visible
		background rgba(0,0,0,0.9)
		padding-top pxtorem(58px)
		padding-bottom pxtorem(30px)
		ul
			width pxtorem(2920px)
			li:last-child
				margin-right pxtorem(40px)
			li
				width pxtorem(164px)
				height pxtorem(226px)
				position relative
				overflow-x auto
				overflow-y hidden
				white-space nowrap
				float left
				margin-left pxtorem(25px)
				img
					width pxtorem(164px)
					height pxtorem(226px)
	.name
		height pxtorem(100px)
		font-size pxtorem(35px)
		line-height pxtorem(100px)
		text-indent pxtorem(30px)
		border-bottom pxtorem(1px) solid gray
		span
			color #ffa641
			font-size pxtorem(30px)
			margin-left pxtorem(10px)
	.tiem
		display flex
		height pxtorem(85px)
		border-bottom pxtorem(1px) solid #ccc
		.sp
			border-bottom pxtorem(5px) solid red
		span
			flex 1
			font-size pxtorem(30px)
			text-align center
			line-height pxtorem(90px)
	.pric
		div
			height pxtorem(120px)
			overflow hidden
			border-bottom pxtorem(1px) solid gray
			span:nth-child(1)
				margin-top pxtorem(38px)
				font-size pxtorem(39px)
				margin-left pxtorem(17px)
			span:nth-child(2)
				margin-top pxtorem(38px)
				margin-left pxtorem(95px)
				font-size pxtorem(33px)
			span:nth-child(4)
				font-size pxtorem(40px)
				margin-right pxtorem(40px)
				margin-top pxtorem(40px)
				color red
				float right
			span:nth-child(3)
				float right
				font-size pxtorem(33px)
				border pxtorem(1px) solid red
				color red
				margin-top pxtorem(25px)
				margin-right pxtorem(35px)
				padding pxtorem(15px)
				border-radius pxtorem(13px)
			span
				float left



</style>



