<template>
<div class="commit">
	<div class="header">
		<div class="header1">
			<img @click="backFn" src="../../../assets/image/movie/back.png">
			<p class="name">{{name.MovieDetailModel.nm}}</p>
			<p class="fa" @click="fabuFn">发布</p>
		</div>
	</div>
	<div class="select">
		<button :class="bgshow ? 'bg' : '' " @click="seeFn">看过</button>
		<button :class="!bgshow ? 'bg' : '' " @click="see1Fn">想看</button>
	</div>
	<p class="p" v-if="seebol">我的评分<span>{{num==0 ? '' : num+'分'}}</span></p>
	<ul v-if="seebol">
		<li v-for="(item,index) in Arr" @click="clickFn(index)">
			<img :src="star[index] ? url[0] : url[1]">
			<p>{{item}}</p>
		</li>
	</ul>
	<div class="ping">
		<textarea placeholder="写几句评价" v-model="mes"></textarea>
	</div>
</div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';

export default {
  data () {
    return {
     name:"",
     Arr:["极差","较差","一般","不错","很棒"],
     bgshow:true,
     num:0,
     star:[false,false,false,false,false],
     url:[require('../../../assets/image/movie/star.png'),require('../../../assets/image/movie/star1.png')],
     seebol:true,
     details:[],
     mes:''
    }
  },
  created(){
  	this.name=this.$route.query.details;
  	// console.log(this.name.CommentResponseModel.cmts);
  	this.details=this.name.CommentResponseModel.cmts;
  },
  methods:{
  	...mapActions([
        'setMovieCommit'
    ]),
  	backFn:function(){
  		this.$router.go(-1);
  	},
  	seeFn:function(){
  		this.bgshow=true;
  		this.seebol=true;
  	},
  	see1Fn:function(){
  		this.bgshow=false;
  		this.seebol=false;
  	},
  	fabuFn:function(){
  		if(this.mes==''){
  			alert('请输入内容');
  			return;
  		}
  		var now = new Date();  
        var year = now.getFullYear();       //年  
        var month = now.getMonth() + 1;     //月  
        var day = now.getDate();            //日  
        var hh = now.getHours();            //时  
        var mm = now.getMinutes();          //分  
        var str={'nickName':'番薯','approve':0,'content':this.mes,'score':this.num,'time':year+"-"+month+"-"+day+' '+hh+":"+mm,'avatarurl':require('../../../assets/image/nav/duoduo.png')}
        this.details.unshift(str);
        this.setMovieCommit(this.details);
        this.$router.push({path:'/details'});
        // console.log(this.details);
  	},
  	clickFn:function(index){
  		var arr=this.star;
  		for(var i=0;i<arr.length;i++){
  					arr[i]=false;
  				}
  		switch (index) {
  			case 0:
  				this.num=1;
  				for(var i=0;i<1;i++){
  					arr[i]=true;
  				}
  				this.star=arr;
  				break;
  			case 1:
  				this.num=2;
  				for(var i=0;i<2;i++){
  					arr[i]=true;
  				}
  				this.star=arr;
  				break;
  			case 2:
  				this.num=3;
  				for(var i=0;i<3;i++){
  					arr[i]=true;
  				}
  				this.star=arr;
  				break;
  			case 3:
  				this.num=4;
  				for(var i=0;i<4;i++){
  					arr[i]=true;
  				}
  				this.star=arr;
  				break;
  			case 4:
  				this.num=5;
  				for(var i=0;i<5;i++){
  					arr[i]=true;
  				}
  				this.star=arr;
  				break;
  			default:
  				// statements_def
  				break;
  		}
  	}
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='stylus' scoped>
@import "../../../assets/common/stylus/common.styl"
.commit
	background white
	.header
		position fixed
		width 100%
		top 0
		left 0
		.header1
			width 100%
			height pxtorem(69px)
			padding-top pxtorem(32px)
			background white
			overflow hidden
			border pxtorem(1px) solid #ccc
			img
				width pxtorem(40px)
				height pxtorem(45px)
				float left
				margin-left pxtorem(34px)
			.fa
				float right
				font-size pxtorem(38px)
				color #0069c1
				margin-right pxtorem(26px)
				margin-top pxtorem(8px)
			.name
				display inline-block
				font-size pxtorem(40px)
				float left
				margin-left pxtorem(60px)
				margin-top pxtorem(6px)
				max-width pxtorem(400px)
				overflow hidden
				white-space nowrap
				text-overflow ellipsis
	.select
		margin-top pxtorem(102px)
		.bg
			background red
		button:nth-child(1)
			margin-left pxtorem(215px)
			margin-right pxtorem(37px)
		button
			width pxtorem(150px)
			height pxtorem(63px)
			text-align center
			line-height pxtorem(63px)
			border 0
			outline none
			border-radius pxtorem(10px)
			font-size pxtorem(32px)
			margin-top pxtorem(68px)
			margin-bottom pxtorem(55px)
			background gray
			color white
	.p
		width 100%
		font-size pxtorem(32px)
		text-align center
		span
			color #ffa641
			margin-left pxtorem(10px)
			font-size pxtorem(32px)
	ul
		display flex
		width pxtorem(570px)
		margin auto
		margin-top pxtorem(42px)
		padding-bottom pxtorem(70px)
		li
			flex 1
			text-align center
			img 
				width pxtorem(73px)
				height pxtorem(69px)
			p
				font-size pxtorem(30px)
	.ping
		border-top pxtorem(1px) solid #ccc
		padding-bottom pxtorem(20px)
		padding-top pxtorem(26px)
		textarea
			width pxtorem(696px)
			min-height pxtorem(150px)
			margin-left pxtorem(30px)
			font-size pxtorem(30px)
			border 0


</style>



