<template>
<div class="address">
	<div class="fixed">
 		<div class="header">
	 	<img src="../../../assets/image/movie/back.png" @click="backFn">
	 	<p>{{name}}</p>
	 	</div>
 	</div>
 	<div class="tiem">
 		 <span :class="bol ? 'sp':'' " @click='dayFn'>今天{{tiem}}</span>
 		 <span :class="!bol ? 'sp':'' " @click="todayFn">明天{{tiem1}}</span>
 	</div>
 	<ul>
 		<li v-for='(item,index) in swarr' @click='selectFn(index)'>
 			<span>{{item.nm}}</span>
 			<span>{{item.price}}  元起</span>
 			<p>{{item.addr}}</p>
 		</li>
 	</ul>
</div>
</template>

<script>
import axios from 'axios';
import localEvent from '../../../store/local';

export default {
  data () {
    return {
     name:'',
     address:[],
     address1:[],
     swarr:[],
     tiem1:"",
     tiem:"",
     bol:true
    }
  },
  created(){
  	var id=localEvent.StorageGetter('id');
  	axios.get('http://xlcyq.sc2yun.com/data/data.php',{
    params: {
      file: 'https://m.maoyan.com/movie/'+id+'.json'}}).then
    (res =>{
    	// if(res.data.data.MovieDetailModel.nm==""){}else{
     //    localEvent.StorageSetter("address1",res);
     //  };
     //  var res=localEvent.StorageGetter("address1");
    	this.name=res.data.data.MovieDetailModel.nm;
    });
    axios.get('http://xlcyq.sc2yun.com/data/data.php',{
    params: {
      file: 'movie/address.json'}}).then
    (res =>{
    	// if(res.data.cinemas==""){}else{
     //    localEvent.StorageSetter("address2",res);
     //  };
     //  var res=localEvent.StorageGetter("address2");
    	this.address=res.data.cinemas;
    	this.swarr=this.address;
    	// console.log(res.data.cinemas)
    });
    axios.get('http://xlcyq.sc2yun.com/data/data.php',{
    params: {
      file: 'movie/address1.json'}}).then
    (res =>{
    	// if(res.data.cinemas==""){}else{
     //    localEvent.StorageSetter("address3",res);
     //  };
     //  var res=localEvent.StorageGetter("address3");
    	this.address1=res.data.cinemas;
    	console.log(res.data.cinemas)
    });
    var now = new Date();  
    var month = now.getMonth() + 1;     //月  
    var day = now.getDate();            //日
    this.tiem=month+'月'+day+'日';
    this.tiem1=month+'月'+(day+1)+'日';
    console.log(this.tiem1)
  },
  methods:{
  	backFn:function(){
  		this.$router.push({path:'/details'})
  	},
  	dayFn:function(){
  		this.bol=true;
  		this.swarr=this.address;
  	},
  	todayFn:function(){
  		this.bol=false;
  		this.swarr=this.address1;
  	},
  	selectFn:function(index){
  		var addr=this.swarr[index];
  		localEvent.StorageSetter('addr',addr);
  		this.$router.push({path:'/select'});
  		// console.log(addr)
  	}
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='stylus' scoped>
@import "../../../assets/common/stylus/common.styl"
.address
	background white
	.fixed
		width 100%
		position fixed
		top 0
		left 0
		z-index 100
		.header
			overflow hidden
			position relative
			top 0
			left 0
			background white
			height pxtorem(77px)
			padding-top pxtorem(25px)
			border-bottom pxtorem(2px) solid #ccc
			img
				width pxtorem(36px)
				height pxtorem(43px)
				float left
				margin-left pxtorem(17px)
			p
				text-align center
				font-size pxtorem(39px)
				margin-top pxtorem(5px)
				overflow hidden
				white-space nowrap
				text-overflow ellipsis
	.tiem
		padding-top pxtorem(105px)
		display flex
		height pxtorem(85px)
		border-bottom pxtorem(1px) solid #ccc
		.sp
			border-bottom pxtorem(5px) solid red
		span
			flex 1
			font-size pxtorem(30px)
			text-align center
			line-height pxtorem(90px)
	ul
		li
			height pxtorem(163px)
			padding-top pxtorem(28px)
			border-bottom pxtorem(1px) solid #ccc
			margin-left pxtorem(33px)
			span:nth-child(2)
				color red
				font-size pxtorem(32px)
				margin-left pxtorem(32px)
			span
				font-size pxtorem(35px)
				color black
			p
				width pxtorem(584px)
				font-size pxtorem(30px)
				color gray
				margin-top pxtorem(39px)
				overflow hidden
				text-overflow ellipsis
				white-space nowrap
			

</style>



