<template>
  <div class="baiduMap">
  <img src="../../assets/image/my/back.png" alt="" class="back" @click="backFn()">
  	<div class="search">
  		<input type="text" @input="searchMapFn()" @focus="searchBolFn()" @blur="smallSeachBolFn()" placeholder="搜索地点">
  		<span v-show="cancelBol" @click="cancelBol=searchBol=false">取消</span>
  		<span v-show="smallSeachBol">搜索</span>
  	</div>
  	<div style="position:relative">
	  	<div id="mapContainer"></div>
	  	<img src="../../assets/image/my/marker.png" alt="" class="marker">
  	</div>
  	<ul class="searchList" v-show="searchBol">
  		<!-- <li v-for="v in addressList">{{v.name}}</li> -->
  		<!-- <li v-for="v in poistionAddressList">{{v.addr}}</li> -->
  		<!-- <li v-for="(v,k) in addressList">{{addressList[k]['name']}}</li> -->
  		<li v-for="(v,k) in addressList" @click="selAddressFn(k)">
			<img src="../../assets/image/my/search.png" alt="">
  			<span>{{v.name}}</span><br>
  			<span>{{poistionAddressList[k]}}</span>
  		</li>
  	</ul>
  	<ul>
  		
  	</ul>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';
export default {
  data () {
    return {
     	cityName:'',
     	addressList:[],
     	poistionAddressList:[],
     	searchBol:false,
     	cancelBol:false,
     	smallSeachBol:false
    }
  },
  methods:{
  	...mapActions({
  		setUserLocation:'setUserLocation'
  	}),
  	searchMapFn:function(){
  			if(event.currentTarget.value.trim()==""){
  				this.addressList=[];
  				this.poistionAddressList=[];
  				this.searchBol=true;
  				this.smallSeachBol=false;
  				this.cancelBol=true;
  				return;
  			}else{
  				this.smallSeachBol=true;
  				this.cancelBol=false;
  			}
  			var that= this;
  			$.ajax({
			  type: "GET",
			  url: "http://api.map.baidu.com/place/v2/suggestion?query="+event.currentTarget.value+"&region="+'广州'+"&city_limit=true&output=json&ak=ehLip8uiECLgXxMi5SQo1NEwcKHS6m2D",
			  dataType: "jsonp",
			  success:function(res){
			  	console.log(res)
			  	that.addressList=res.result;
			  	if(that.addressList.length==0){return}
			  var arr=[];
			  	for(var i=0;i<that.addressList.length;i++){
			  			if(!Boolean(that.addressList[i].location)){
			  				arr.push("");
			  				continue;
			  			}
			  			$.ajax({
				  		  type: "GET",
						  url: "http://api.map.baidu.com/geocoder/v2/?&location="+that.addressList[i].location.lat+","+that.addressList[i].location.lng+"&output=json&pois=0&ak=ehLip8uiECLgXxMi5SQo1NEwcKHS6m2D",
						  dataType: "jsonp",
						  success:function(res){
						  	console.log(res)
						  	arr.push(res.result.formatted_address)
						  	// that.poistionAddressList=res.result.pois;
						  	
						  }
					  		
					  	})
			  	}
			  				  // 	var str="";
			  // //第0无坐标
			  // 	for(var i=1;i<that.addressList.length;i++){
			  // 		str+=that.addressList[i].location.lat+",";
			  // 		str+=that.addressList[i].location.lng;
			  // 		if(i==that.addressList.length-1){

			  // 		}else{
			  // 			str+="|"
			  // 		}
			  		
			  // 	}
			  	// $.ajax({
		  		//   type: "GET",
				  // url: "http://api.map.baidu.com/geocoder/v2/?batch=true&location="+str+"&output=json&pois=0&ak=ehLip8uiECLgXxMi5SQo1NEwcKHS6m2D&extensions_town=true",
				  // dataType: "jsonp",
				  // success:function(res){
				  // 	console.log(res)
				  // 	// that.poistionAddressList=res.result.pois;
				  	
				  // }
			  		
			  	// })
			  	that.poistionAddressList=arr
			  },
			  error:function(res){
			  	console.log("请求错误")
			  }
			});
  	},
  	selAddressFn:function(index){
  		this.setUserLocation(
  			{address:this.addressList[index].name,town:this.poistionAddressList[index]}
  		)
  		this.$router.push('/editAddress')
  		
  	},
  	searchBolFn:function(){
  		if(event.currentTarget.value.trim()==""){
  			this.cancelBol=true;
  			this.smallSeachBol=false;
  			this.searchBol=true
  		}
  	},
  	smallSeachBolFn:function(){
  		if(event.currentTarget.value.trim()==""){
  			this.cancelBol=true;
  			this.smallSeachBol=false;
  		}
  	},
  	backFn:function(){
  		this.$router.go("-1");
  	}
  },
  mounted:function(){
  	var map = new BMap.Map("mapContainer");
  	map.addControl(new BMap.GeolocationControl());    
  	var that = this;
	$.ajax({
	  type: "GET",
	  url: "https://api.map.baidu.com/location/ip?ak=ehLip8uiECLgXxMi5SQo1NEwcKHS6m2D&coor=bd09ll",
	  dataType: "jsonp",
	  success:function(res){
	  	// console.log(res)
	  	that.cityName=res.content.address_detail.city
	  	// console.log(res.content)
	  	var point = new BMap.Point(res.content.point.x,res.content.point.y);
	  	map.centerAndZoom(point, 15); 
	  	map.addEventListener("dragend", function(){    
		    var center = map.getCenter();    
		    // console.log("地图中心点变更为：" + center.lng + ", " + center.lat); 
		    $.ajax({
	  		  type: "GET",
			  url:"http://api.map.baidu.com/geocoder/v2/?callback=renderReverse&location="+center.lng+","+center.lat+"&output=json&pois=1&ak=ehLip8uiECLgXxMi5SQo1NEwcKHS6m2D",
			  dataType: "jsonp",
			  success:function(res){
			  	console.log(res)
			  	
			  }
		  		
		  	})
		}
		);
	  },
	  error:function(res){
	  	console.log("错误")
	  }
	});


  },
  components:{
 
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='stylus' scoped>
@import "../../assets/common/stylus/common.styl";
input::-webkit-input-placeholder{text-align: center;}
#mapContainer
	height pxtorem(500px)
.search
	width pxtorem(750px)
	height pxtorem(90px)
	background white
	overflow hidden
	input
		width pxtorem(580px)
		height pxtorem(60px)
		border none
		outline none
		background #ebebeb
		font-size pxtorem(26px)
		margin pxtorem(15px) 0 0 pxtorem(85px);
		border-radius pxtorem(15px)
		text-indent 1em
	span
		color blue
		font-size pxtorem(26px)
		margin-left pxtorem(10px)
.searchList
	position absolute
	width pxtorem(750px)
	height pxtorem(1242px)
	background white
	top pxtorem(90px)
	left 0
	z-index 10
	border-top 1px solid black
	li
		padding pxtorem(30px)
		border-bottom 1px solid gray
		img
			width pxtorem(32px)
			height pxtorem(32px)
			vertical-align middle
		span:first-of-type
			font-size pxtorem(30px)
			font-weight bolder
		span:nth-of-type
			font-size pxtorem(24px)
.marker
	position absolute
	top 0
	left 0
	right 0
	bottom 0
	margin auto
	width pxtorem(50px)
	height pxtorem(70px)
.back
	position absolute
	width pxtorem(40px)
	height pxtorem(40px)
	left pxtorem(30px);
	top pxtorem(26px);

</style>



