<template>
  <div class="seach">
    <my-header title="搜索">
    	<span slot="left">
            <img src="../../assets/image/home/back.png" @click="backFn">
        </span>  
    </my-header>
    <div class="div1">
    	<img src="../../assets/image/home/seach.png" class="img">
    	<input type="text" class="txt1" placeholder="搜索/关键字"  @input="seachFn" ref="val">
    	<span @click="backFn">取消</span>
    </div>
    <div class="content" v-for="(item,index) in seachArr" @click="godetail({num:index,src:item.pic_url,name:item.name})" >
        <img :src="item.pic_url" class="shopimg">
        <div>
          <p class="name">{{item.name}}</p>
          <div class="mes">
            <p>
              <img src="../../assets/image/home/star.png" v-for="item in num[index]">
              <img src="../../assets/image/home/star2.png" v-for="item in nums[index]">
              <img src="../../assets/image/home/star1.png" v-for="item in num1[index]">
              <!-- <img src="../../assets/image/home/star1.png" class="star2"> -->
              <strong>月售{{item.month_sale_num}}</strong>
              <span>{{item.mt_delivery_time}}|{{item.distance}}</span>
            </p>
            <p>{{item.min_price_tip}}|{{item.shipping_fee_tip}}|{{item.average_price_tip}}</p>
          </div>
        </div>
      </div>
      
  </div>
</template>

<script>
import Header from "../share/header.vue";
import axios from 'axios';
import {mapGetters,mapActions} from "vuex";
import localEvent from '../../store/local';
export default {
  data () {
    return {
	    shopArr:[],
	    num:[],
	    nums:[],
	    num1:[],
	    score:[],
	    seachArr:[],
	    hisArr:[]
    }
  },
  components:{
  	"my-header":Header, 
  },
  computed:mapGetters({
   	 head:"getNum"
  }),
  methods:{
  	...mapActions([
     	 "setNum"
      ]),	
    seachFn:function() {
    	this.seachArr = []
    	for (var i = 0; i < this.shopArr.length; i++) {
    		if (this.shopArr[i].name.indexOf(this.$refs.val.value) != -1) {
    			console.log(this.$refs.val.value)
    			this.seachArr.push(this.shopArr[i])
    		}
    	}
    	if (this.$refs.val.value=="") {this.seachArr = []}
    },
    backFn:function() {
    	this.$router.push({path:"./"});
    },
    numFn:function(data) {
    	for (var i = 0; i < data.length; i++) {
    		this.shopArr.push(data[i])
    	}
    },
    axiosFn:function(name) {
    	axios.get('http://xlcyq.sc2yun.com/data/data.php',{
   		 params: {
     		 file:name
    	}}).then(res => {
      //   if(res.data.data.poilist==""){}else{
      //   localEvent.StorageSetter("seach",res);
      // };
      // var res=localEvent.StorageGetter("seach");
    		for (var i = 0; i < res.data.data.poilist.length; i++) {
    		this.score.push(res.data.data.poilist[i].wm_poi_score)
    		}
    		this.star();
      		this.numFn(res.data.data.poilist)
   		 });
    },
    godetail:function(index){
      this.setNum(index);
      this.$router.push({path:"./detail"});
    },
    star:function (){
        for (var i = 0; i < this.score.length; i++) {
        var s = this.score[i] 
        this.num[i]=parseInt(s);
        var tt=s-this.num[i];
        if(tt>=0.5 && tt<=0.99){
         this.nums[i]=1;
        }else{
         this.nums[i]=0;
        }
        this.num1[i]=5-this.num[i]-this.nums[i];
      }
    }
  },
mounted: function () {
    
 },
created(){
  this.axiosFn('list/kuaican_07.json');
  this.axiosFn('list/zhengcan_06.json');
  }
}
</script>


<style lang='stylus' scoped>
@import '../../assets/common/stylus/common.styl'
.seach
	padding-top pxtorem(90px)
	.div1
		background-color rgba(155,155,151,.1)
		padding-bottom pxtorem(20px)
		.img
			position relative
			left pxtorem(70px) 
			top pxtorem(14px)
			width pxtorem(50px)
		.txt1
			width 70%
			font-size pxtorem(32px)
			height pxtorem(60px)
			background-color white
			outline none
			text-indent 1.2rem
			margin-top pxtorem(20px)
			border-radius pxtorem(10px)
		span
			font-size pxtorem(40px)
			margin-left pxtorem(20px)
	.div2
		width pxtorem(700px)
		margin-left pxtorem(25px)
		text-align center
		p
			font-size pxtorem(36px)
			margin-top pxtorem(20px)
			color #ccc
		button
			width pxtorem(230px)
			font-size pxtorem(28px)
			margin-top pxtorem(20px)
			border-radius pxtorem(40px)
			outline none
			background-color white
	.content
		height pxtorem(200px)
		margin-top pxtorem(15px)
		margin-bottom pxtorem(15px)
		text-align left
		position relative
		border-bottom 1px solid #ccc
		.shopimg
			height pxtorem(150px)
			width pxtorem(200px)
			margin-left pxtorem(20px)
			vertical-align middle
		div 
			display inline-block
			margin-left pxtorem(20px)
			vertical-align middle
		.name
			margin-left pxtorem(20px)
			margin-top pxtorem(20px)
			font-size pxtorem(28px)
		.mes
			height pxtorem(150px)
			p
				margin-top pxtorem(16px)
				img
					height pxtorem(20px)
					margin-left pxtorem(10px)
					vertical-align middle
				span
					vertical-align middle
				strong 
					margin-right pxtorem(40px)

</style>



