import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'

import Homepage from '@/components/Homepage/Homepage'
import CityList from '../components/HomePage/CityList'
import seach from '../components/HomePage/seach'
import detail from '../components/HomePage/detail'
import Find from '@/components/Find/Find'
import Details from '@/components/Find/Details/Details'
import Commit from '@/components/Find/Commit/Commit'
import Address from '@/components/Find/Address/Address'
import Select from '@/components/Find/Select/Select'
import People_commit from '@/components/Find/People_commit/People_commit'
import Order from '@/components/Order/Order'
import My from '@/components/My/My'
import Seat from '@/components/Find/Select/Seat'
import Selectdata from '@/components/Homepage/Selectdata'
import Login from '@/components/My/Login'
import address from '@/components/My/Address'
import PersonalInfo from '@/components/My/PersonalInfo'
import InitPassword from '@/components/My/InitPassword'
import EditAddress from '@/components/My/editAddress'
import BaiduMap from '@/components/My/BaiduMap'

 
Vue.use(Router) 

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Homepage
    },
    {
      path: '/seach',
      component: seach
    },
    {
      path: '/city',
      component: CityList
    },
    {
      path: '/detail',
      component: detail
    },
    {
      path: '/find',
      component: Find
    },
    {
      path:'/details',
      component:Details
    },
    {
      path:'/commit',
      component:Commit
    },
    {
      path:'/address',
      component:Address
    },
    {
      path:'/select',
      component:Select
    },
     {
      path:'/seat',
      component:Seat
    },
    {
      path:'/people_commit',
      component:People_commit
    },
    {
      path: '/order',
      component: Order
    },
    {
      path: '/my',
      component: My
    },
     {
      path: '/selectdata',
      component: Selectdata
    },
    {
      path: '/login',
      component: Login
    },
    {
      path:'/myaddress',
      component:address
    },
    {
      path:'/personalInfo',
      component:PersonalInfo
    },
    {
      path:'/initPassword',
      component:InitPassword
    },
    {
      path:'/editAddress',
      component:EditAddress
    },
    {
      path:'/baiduMap',
      component:BaiduMap
    }
  ]
})
 


