               <div></div>
          </li>
    </ul>
    <my-nav></my-nav>
    测试order
  </div>
</template>

<script>
import Nav from '../Nav.vue';
import axios from 'axios';
export default {
  data () {
    return {
        
      Arr:[
          {title:"如意馄饨(东湖国际酒店)",url:require("../../assets/image/people10.jpg"),title2:"订单已完成",
          title3:"香辣爆炒度宝再放",num:1,price:12},

          {title:"如意馄饨(东湖国际酒店)",url:require("../../assets/image/people10.jpg"),title2:"订单已完成",
          title3:"香辣爆炒度宝再放",num:1,price:12},

          {title:"如意馄饨(东湖国际酒店)",url:require("../../assets/image/people10.jpg"),title2:"订单已完成",
          title3:"香辣爆炒度宝再放",num:1,price:12},

          {title:"如意馄饨(东湖国际酒店)",url:require("../../assets/image/people10.jpg"),title2:"订单已完成",
          title3:"香辣爆炒度宝再放",num:1,price:12}
      ]
    }
  },
  components:{
    'my-nav':Nav
  },
  created(){
     axios.get('http://xlcyq.sc2yun.com/data/data.php',{
        params:{
          file:'order.json'
        }}).then(res=>{
          console.log(res.data.data.poilist[0].average_price_tip);
     })
  },
  mounted:function(){
    console.log('aaaaaa')
  }
}
