<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="stylus" scoped>
@import '../../assets/common/stylus/common.styl'
.content
  background #ebebeb
  .allul 
    margin-bottom pxtorem(21px)
    background #ffffff  
    .first
         img,span:nth-of-type(1),span:nth-of-type(2)
              margin-top  pxtorem(40px) 
              float left 
         img 
               width  pxtorem(90px)
               height  pxtorem(90px)
               border-radius 50% 
               margin-left pxtorem(30px)
               
         span:nth-of-type(1)
               font-size pxtorem(30px)
               margin-left pxtorem(11px)
               margin-top  pxtorem(80px)
               
         span:nth-of-type(2)
               margin-left pxtorem(132px)
               margin-top  pxtorem(80px)
