              height  pxtorem(2px)
              background #f1f2f2
              margin-left pxtorem(10px)
       
       span
              display   block
              height    pxtorem(50px)
              width     pxtorem(132px) 
              border pxtorem(1px) solid #d7d7d7
              line-height  pxtorem(50px)
              margin-top pxtorem(18px)
              margin-left pxtorem(545px)
        div
              width pxtorem(750px)
              height pxtorem(20px)
  .allul:nth-last-of-type(2)
    margin-bottom pxtorem(100px)
               
   
</style>



