  		<p>{{this.head.name}}</p>
  	</div>
  	<div class="chose">
  		<a v-for="(item,index) in choseArr" @click="choseFn(index)"><span ref="line">{{item}}</span></a>
  	</div>
  	<div class="goodList chosemes" ref="good">
  		<div class="big">
  			<div v-for="(item,index) in menu" class="menu" @click="menuFn(index,item.spus)" ref="menu">
  				<img :src="item.icon">
  				<span>{{item.name.length>=4?item.name.slice(2):item.name}}</span>
  			</div>
  		</div>
  		<div class="right">
  			<div class="shop" v-for="(item,index) in shop">
  				<img :src="item.picture">
  				<div class="detail">
  					<p class="name">{{item.name}}</p>
  					<span>{{item.month_saled_content}}&nbsp&nbsp{{item.praise_content}}</span>
  					<div class="money">
  						￥{{item.min_price}}
  						<button class="add" @click="addFn(index,shop)">+</button>
  						<span  ref="val" style="display:none;" class="many">{{mes[index]}}</span>
	  					<button class="sub" style="display:none;" @click="subFn(index,shop)" ref="btn">-</button>
  					</div>
  				</div>
  			</div>
  		</div>
  		<div class="car" ref="car">
  			<img src="../../assets/image/home/car.png">
  			<span>您的购物车空空如也~~</span>
  			<strong>￥0起送</strong>
  		</div>
  		<div class="car1" style="display:none;" ref="car1">
  			<img src="../../assets/image/home/car1.png" @click="shopCarFn">
  			<p>￥{{sum}}</p>
  			<em>{{send}}</em>
  			<button ref="sum" @click="payFn">结算</button>
  		</div>
  	</div>
  	<div class="comment chosemes" ref="good" style="display:none;">
  		<div class="comment_1">
  			<ul class="left">
  				<li>{{comment.comment_score}}</li>
  				<li>商家评价</li>
  			</ul>
  			<ul class="cent">
  				<li>
  					口味
  					<img src="../../assets/image/home/star.png">
  					<img src="../../assets/image/home/star.png">
  					<img src="../../assets/image/home/star.png">
  					<img src="../../assets/image/home/star.png">
              		<img src="../../assets/image/home/star2.png">
  					<span>{{comment.food_score}}</span>
  				</li>
  				<li>
  					包装
  					<img src="../../assets/image/home/star.png">
  					<img src="../../assets/image/home/star.png">
  					<img src="../../assets/image/home/star.png">
  					<img src="../../assets/image/home/star.png">
              		<img src="../../assets/image/home/star2.png">
  					<span>{{comment.pack_score}}</span>
  				</li>
  			</ul>
  			<ul class="right1">
  				<li>{{comment.delivery_score}}</li>
  				<li>配送评分</li>
  			</ul>
  		</div>
  		<div class="comment_2">
  			<div  v-for="(item,index) in categories">
  				<button ref="but" @click="butFn(index)">{{item}}</button>
  			</div>
  		</div>
  		<div class="comment_3">
  			<ul>
  				<li v-for="(item,index) in commentsArr" class="adv">
  					<img :src="item.user_pic_url" :onerror="url" class="user">
  					<div>
  						<p>
  							<strong>{{item.user_name}}</strong>
  							<span>{{item.order_time}}</span>
  						</p>
  						<p>
  							<img src="../../assets/image/home/star.png" v-for="star in num[index]">
              				<img src="../../assets/image/home/star2.png" v-for="star in nums[index]">
              				<img src="../../assets/image/home/star1.png" v-for="star in num1[index]">
              				<span>{{item.ship_time}}分钟送达</span>
  						</p>
  						<p>{{item.comment}}</p>
  						<span v-for="comment_pics in  item.comment_pics" class="comment_pics">
  							<img :src="comment_pics.url">
  						</span>
  					</div>
  				</li>
  			</ul>
  		</div>
  	</div>
  	<div class="comment_4 chosemes" style="display:none;" ref="good">
  		<div class="shopHome">
  			<div class="div1">
  				<img src="../../assets/image/home/phone.png">
  				<span>4006927927</span>
  				<strong><img src="../../assets/image/home/phone.png"></strong>
  			</div>
  			<div class="div2">
  				<span><img src="../../assets/image/home/place.png">商家地址： </span>
  				<p>广州市天河区下元岗燕岭路中元广场241号</p>
  			</div>
  		</div>
  		<div class="shopHome1">
  			<div class="div3">
  				<img src="../../assets/image/home/time.png">
  				<span>配送时间：{{shopmes.shipping_time}}</span>
  			</div>
  			<div class="div4">
  				<img src="../../assets/image/home/send.png">
  				<span>配送服务：{{shopmes.app_delivery_tip}}</span>
  			</div>
  		</div>
  		<div class="shopHome2" >
  			<div v-for="item in shopmes.discounts2" class="div4">
  				<img :src="item.icon_url">
  				<span>{{item.info}}</span>
  			</div>
  		</div>
  	</div>
  	<div id="shopCar" style="display:none;">
  		<p><span>购物车</span><strong @click="clearFn">清空</strong></p>
  		<div class="shopping" v-for = "(item,index) in shopping">
  			<em>
  				{{item.name}}
  			</em>
  			<div>
  				<strong>￥{{item.money}}</strong>
  				<button class="sub1" @click="shopsubFn(index,shop)" ref="btn">-</button>
  				<span  ref="val">{{item.many}}</span>
	  			<button class="add1" @click="shopaddFn(index,shop)">+</button>
  			</div>
  		</div>
  	</div>
  </div>
</template>

<script>
import axios from 'axios';
import {mapGetters,mapActions} from "vuex"
import { MessageBox } from 'mint-ui';
import localEvent from '../../store/local';
export default {
  data () {
    return {
     choseArr:['点菜','评价','商家'],
     menu:"",
     goodList:"",
     send:'',
     sum:"",
     comment:'',
     num:[],
     nums:[],
     num1:[],
     kow:'',
     bao:'',
     categories:'',
     commentsArr:'',
     url:'this.src="' + require('../../assets/image/home/logo.jpeg') + '"',
     score:[],
     shopmes:'',
     shop:'',
     shopping:[],
     bol:true,
     mes:[],
     indexs:0,
     shuju:''
    }
  },
  computed:{
    ...mapGetters({
      head:"getNum"
    })   
  },
  methods:{
  	...mapActions([//展开mapActions
      "setNum"
      ]),
  	backFn:function() {
  		 this.$router.go(-1);
  	},
  	choseFn:function (index) {
  		for (var i = 0; i < $('.chosemes').length; i++) {
  			$('.chosemes').eq(i).css("display","none")
  		}
  			$('.chosemes').eq(index).css("display","block")
  		for (var i = 0; i < this.$refs.line.length; i++) {
  			this.$refs.line[i].style.borderBottom ="none";
  		}
  		this.$refs.line[index].style.borderBottom = "8px solid yellow"
  	},
  	menuFn:function(index,shopList){
  		var $bodyH=$('.right').height()/this.menu.length*index;
  		 $(".right").animate({scrollTop:$bodyH*8},200); 
  		for (var i = 0; i < this.$refs.menu.length; i++) {
  			this.$refs.menu[i].style.backgroundColor = "rgba(155,155,155,.2)"
  		}
  			this.$refs.menu[index].style.backgroundColor = "white"
  	},
  	addFn:function(index,item){
  		this.indexs=index;
  		var num = parseInt(this.mes[index]);
  		this.$refs.btn[index].style.transform='rotateZ(360deg)';
  		num++;
  		this.mes[index]=num;
  		this.sumFn(num,item);
  		$('.many').eq(index).css("display","block").animate({"opacity":1,"right":"1.067rem"},0.5)
  		$('.sub').eq(index).css('display','block').animate({'rotate':'360deg','right':'1.74rem','opacity':1},0.5)

  	},
  	subFn:function(index,item){
  		var num = parseInt(this.mes[index]);
  		num--;
  		if (num<=0) {
  			$('.many').eq(index).css("display","none").animate({"opacity":0,"right":"0.4rem"},0.5)
  			$('.sub').eq(index).animate({'rotate':'360deg','right':'0.4rem','opacity':0},0.5,function(){
  					setTimeout(function(){
  						$('.sub').eq(index).css({'display':'none'})
  					},500)
  			})
  		}
  		this.mes[index]=num;
  		this.sumFn(num,item);
  	},
  	shopaddFn:function(index){
  		var num=this.shopping[index].many
  		num++;
  		this.shopping[index].many=num;
  		console.log(this.shopping);
  	},
  	shopsubFn:function(index){
  		var num=this.shopping[index].many
  		num--;
  		this.shopping[index].many=num;
  	},
  	clearFn:function() {
  		this.shopping = [];
  	},
  	shopCarFn:function () {
  		if (this.bol) {
  			$("#shopCar").css('display','block')
  		}else {
  			$("#shopCar").css('display','none')
  		}
  		this.bol=!this.bol;
  		// 赋值到页面
  		var arr=this.shop;
  		var arrindex=[]
  		for (var i = 0; i < this.shopping.length; i++) {
  			for (var j = 0; j < this.shop.length; j++) {
  				if (this.shopping[i].name==this.shop[j].name) {
  					arrindex.push(j)
  				}
  			}
  			
  		}
  		console.log(arrindex)
  	},
  	sumFn:function(index,item){
  		var num = 0;
  		this.shopping=[]
  		for (var i = 0; i < this.mes.length; i++) {
  			if (parseInt(this.mes[i])>0){
  				$(".car").css("display","none")
  				$(".car1").css("display","block")
  				num += parseInt(item[i].min_price) * parseInt(this.mes[i])
  				this.shopping.push({money:item[i].min_price,many:this.mes[i],name:item[i].name,'index':this.indexs})

  			}
  			
  		 }	
  		 console.log(this.shopping)
  		this.sum = num
  		if (this.sum==0) {
  			$(".car1").css("display","none")
  			$(".car").css("display","block")
  		}
  	},
  	star:function (){
        for (var i = 0; i < this.score.length; i++) {
        var s = this.score[i] 
        this.num[i]=parseInt(s);
        var tt=s-this.num[i];
        if(tt>=0.5 && tt<=0.99){
         this.nums[i]=1;
        }else{
         this.nums[i]=0;
        }
        this.num1[i]=5-this.num[i]-this.nums[i];
      }
    },
    butFn:function (index) {
    	for (var i = 0; i < this.$refs.but.length; i++) {
    	 	this.$refs.but[i].style.backgroundColor ="white";
    	 	this.$refs.but[i].style.borderColor= '#ccc';
    	 	this.$refs.but[i].style.color ="#656565";
    	 } 
    	 this.$refs.but[index].style.backgroundColor ="#FFF8E9"; 
    	 this.$refs.but[index].style.borderColor= '#FFB000'; 
    	 this.$refs.but[index].style.color ="#FFB000";
    },
    payFn:function() {
    	console.log(111)
    	MessageBox('结算', '您共需支付'+this.sum+"元");
    }
  },
  created(){
    var str=localEvent.StorageGetter("delies");
    var str1='zhuye/0'+ (this.head.num+1) +'.json';
    if(str==""){
       this.shuju=str1;
    }else{
      this.shuju=str;
    }
    axios.get('http://xlcyq.sc2yun.com/data/data.php',{
    params: {
      file:this.shuju
    }}).then(res => {
      // if(res.data.data.food_spu_tags==""){}else{
      //   localEvent.StorageSetter("detail1s",res);
      // };
      
      // var res=localEvent.StorageGetter("detail1s");
    	this.menu = res.data.data.food_spu_tags;
    	 var arr = [];
    	for (var i = 0; i < res.data.data.food_spu_tags.length; i++) {
    		for (var j = 0; j < res.data.data.food_spu_tags[i].spus.length; j++) {
    			arr.push(res.data.data.food_spu_tags[i].spus[j])
    		}
    	}
    	var ii=[];
    	for(var i=0;i<arr.length;i++){
    		ii.push(0);
    	};
    	this.mes=ii;
    	this.shop = arr;
    	this.send = res.data.data.shopping_cart.shipping_fee_cart_tip;
    	this.goodList = res.data.data.food_spu_tags[0].spus;
      	this.shopmes = res.data.data.poi_info;
      	// console.log(this.shop);
    }); 
     axios.get('http://xlcyq.sc2yun.com/data/data.php',{
    params: {
      file:'pingjia/pingjia2.json'
    }}).then(res => {
      // if(res.data.data==""){}else{
      //   localEvent.StorageSetter("detail2s",res);
      // };
      // var res=localEvent.StorageGetter("detail2s");
    	this.comment = res.data.data;
    	this.kou = res.data.data.food_score;
    	this.bao = res.data.data.pack_score;
    	this.categories = res.data.data.comment_categories
    	this.commentsArr= res.data.data.comments
    	for (var i = 0; i < res.data.data.comments.length; i++) {
    		this.score.push(res.data.data.comments[i].order_comment_score)
    	}
    	this.star();
    	this.orderScore = res.data.data.comments.order_comment_score
    });  
  }
}
</script>

