<template>
<div class="editAddress">
  <commonHeader title="新增收货地址"></commonHeader>
  <div class="save" @click="saveFn()">保存</div>
  <h3>联系人</h3>
  <table class="contact">
  	<tr>
  		<td>姓名：</td>
  		<td><input type="text" placeholder="请填写收货人姓名" ref="editName"></td>
  	</tr>
  	<tr>
  		<td></td>
  		<td class="sex">
  			<span :class="sexBol?'sexBol':'circle'" @click="sexBol=!sexBol"></span>先生<span :class="sexBol?'circle':'sexBol'" @click="sexBol=!sexBol"></span>女士
  		</td>
  	</tr>
  	<tr>
  		<td>电话：</td>
  		<td><input type="text" placeholder="请填写收货手机号码" ref="editPhone"></td>
  	</tr>
  </table>
  <h3>收货地址</h3>
  <table class="address">
  	<tr>
  		<td>小区/大厦/学校：</td>
  		<td>
  			<router-link tag="div" to="/baiduMap" @click.native="addressArrFn()">{{getUserLocation.town}}{{getUserLocation.address}}</router-link>
  		</td>
  	</tr>
  	<tr>
  		<td>楼号－门牌号：</td>
  		<td><input type="text" placeholder="例16号楼427室" ref="editHouseNum"></td>
  	</tr>
  </table>
</div>
</template>

<script>
import commonHeader from './commonHeader'
import { mapGetters, mapActions } from 'vuex';
export default {
  data () {
    return {
     sexBol:true
    }
  },
  computed:{
  	...mapGetters({
  		getUserLocation:'getUserLocation'
  	})
  },
  created:function () {
  	// console.log('create')
  },
  components:{
    'commonHeader':commonHeader	
  },
  methods:{
  	...mapActions([
  		"setAddressArr"
  		]),
  	saveFn:function(){
  		if(editName==""||Number(editHouseNum)||Number(editPhone)||editAddress==""){
			alert("错误");
			return ;
  		}
  		var editName=this.$refs.editName.value;
  		var editHouseNum=this.$refs.editHouseNum.value;
  		var editPhone=this.$refs.editPhone.value;
  		var editAddress=this.getUserLocation.town+this.getUserLocation.address;
  		var editSex;
  		if(this.sexBol){
  			editSex="先生"
  		}else{
  			editSex="女士"
  		}
  		var json={
  			editName:editName,
  			editHouseNum:editHouseNum,
  			editPhone:editPhone,
  			editAddress:editAddress,
  			editSex:editSex
  		};
  		this.setAddressArr(json);
  		this.$router.push("/myAddress");
  		localStorage.setItem("addressArr","")
  	},
  	addressArrFn:function(){
  		var editName=this.$refs.editName.value;
  		var editHouseNum=this.$refs.editHouseNum.value;
  		var editPhone=this.$refs.editPhone.value;
  		var editAddress=this.getUserLocation.town+this.getUserLocation.address;
  		var editSex=this.sexBol;
  		var json={
  			editName:editName,
  			editHouseNum:editHouseNum,
  			editPhone:editPhone,
  			editAddress:editAddress,
  			editSex:editSex
  		};
  		localStorage.setItem("addressArr",JSON.stringify(json))
  	}
  },
  mounted:function(){
  	if(localStorage.getItem("addressArr")){
  		var json=JSON.parse(localStorage.getItem("addressArr"));
  		this.$refs.editName.value=json.editName;
  		this.$refs.editHouseNum.value=json.editHouseNum;
  		this.$refs.editPhone.value=json.editPhone;
  		this.sexBol=json.editSex;
  	}
  }
}
</script>


<style lang="stylus" scoped>
@import "../../assets/common/stylus/common.styl";
.sexBol
	display inline-block
	width pxtorem(36px)
	height pxtorem(36px)
	background:url("../../assets/image/my/gou.png") no-repeat center 
	background-size pxtorem(36px)
	vertical-align middle
.save
	position absolute
	right pxtorem(30px)
	top pxtorem(26px)
	font-size pxtorem(26px)
h3
	padding-left pxtorem(30px)
	height pxtorem(70px)
	line-height pxtorem(70px)
.contact
	background white
	tr
		td:first-child
			padding-left pxtorem(30px)
			width pxtorem(100px)
		td:last-child
			width pxtorem(620px)
			input
				height pxtorem(84px)
				width pxtorem(600px)
				border none
				outline none
				font-size pxtorem(28px)			
		.sex
			border-top 1px solid gray
			border-bottom 1px solid gray
			padding pxtorem(20px) 0
			span
				margin 0 pxtorem(20px)
			.circle
				display inline-block
				width pxtorem(36px)
				height pxtorem(36px)
				border 2px solid gray
				border-radius 50%
				vertical-align middle
.address
	background white
	tr
		td:first-child
			padding-left pxtorem(30px)
			white-space nowrap
			font-size pxtorem(28px)
		td:last-child
			width pxtorem(350px)
			input,div
				height pxtorem(84px)
				line-height pxtorem(84px)
				width pxtorem(540px)
				border none
				outline none
				font-size pxtorem(28px)

</style>



