<template>
 	<div class="initPassword">
 		<commonHeader title="修改密码"></commonHeader>
 		<form action="javascript:void(0)">
			<input type="password" placeholder="原密码" @focus="focusColorChange()" @blur="blurColorChange()" ref="previousPassword">
			<input type="password" placeholder="新密码" @focus="focusColorChange()" @blur="blurColorChange()" ref="newPassword">
			<input type="password" placeholder="确认新密码" @focus="focusColorChange()" @blur="blurColorChange()" ref="rePassword">
			<input type="submit" value="确认提交" @click="initPasswordFn()" ref="initPassword">
		</form>
 	</div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';
import commonHeader from './commonHeader'
export default {
  data () {
    return {
     
    }
  },
  methods:{
    ...mapActions({
      updatePassword:'updatePassword',
      setLoginFn:"setLoginFn"
    }),
  	focusColorChange:function(){
  		event.currentTarget.style.border="1px solid #FFD356";
  	},
  	blurColorChange:function(){
  		event.currentTarget.style.border="1px solid black"
      if(this.$refs.rePassword.value.length>6&&this.$refs.newPassword.value.length>6){
          this.$refs.initPassword.style.background = '#FFD356';
      }else{
          this.$refs.initPassword.style.background = '#ccc';
      }
  	},
  	initPasswordFn:function(){
      if(this.$refs.rePassword.value!=this.$refs.newPassword.value){
          this.$refs.rePassword.value="";
          this.$refs.newPassword.value="";
          alert("两次输入密码不一致");
          return;
      }
      var that=this;
  		 var str=JSON.parse(localStorage.getItem('meituanUser'));
       str.password=this.$refs.previousPassword.value;
       str.rePassword=this.$refs.rePassword.value;
       console.log(str)
       this.updatePassword(str).then(function(res){
        if(res.bol==0){
          alert("原始密码错误")
        }else if (res.bol==1) {
          that.setLoginFn({});
          localStorage.setItem("meituanUser",0);
          that.$router.push('/login')
          alert("密码修改成功，请重新登录")
        }else{
          alert("系统错误！！！！")
        }
       })
  	}
  },
  computed:{
    ...mapGetters(
      {getLoginInfoArr:'getLoginInfoArr',
       getLoginInfoPass:'getLoginInfoPass' 
      }
      )
  },
  components:{
    'commonHeader':commonHeader	
  },
  mounted:function(){
   

  }
}
//#FFD356
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="stylus" scoped>
@import "../../assets/common/stylus/common.styl";
form
	overflow hidden
input[type=password]
	width pxtorem(670px)
	height pxtorem(80px)
	display block
	margin pxtorem(30px) auto 0
	font-size pxtorem(30px)
	text-indent 1em
	background white
input[type=submit]
	width pxtorem(670px)
	height pxtorem(80px)
	display block
	margin pxtorem(40px) auto 0
	border none
	outline none
	border-radius pxtorem(10px)
	font-size pxtorem(30px)
	color white

</style>



