<template>
  <div class="login" v-model="selected">
  <!-- <div class="login-header">登录注册页</div> -->
  <commonHeader title="登录注册页"></commonHeader>
	<mt-navbar >
	  <mt-tab-item id="1" @click.native="selected='1'" :style="selected=='1'?'border-bottom:7px solid #26a2ff':'border-bottom:7px solid transparent'">
	  	<span class="login-title">登录</span>
	  </mt-tab-item>
	  <mt-tab-item id="2" @click.native="selected='2'" :style="selected=='2'?'border-bottom:7px solid #26a2ff':'border-bottom:7px solid transparent'">
	  	<span class="login-title">注册</span>	  	
	  </mt-tab-item>
	</mt-navbar>

	<!-- tab-container -->
	<mt-tab-container v-model="selected" class="login-tab-container">
	  <mt-tab-container-item id="1">
	    <form action="javascript:void(0)" class="login-info">
	  		<table>
  				<tr>
  					<td>用户名</td>
  					<td><input type="text" placeholder="请输入用户名" ref="loginUsername"></td>
	  			</tr>
	  			<tr>
	  				<td>密码</td>
	  				<td><input type="password" placeholder="请输入密码" ref="loginPassword" @input="regPasswordVerifyFn()"></td>
	  			</tr>
	  			<tr><td colspan='2'><input type="submit" @click="loginFn()"></td></tr>
	  		</table>
	  	</form>
	  </mt-tab-container-item>
	  <mt-tab-container-item id="2">
	    <form action="javascript:void(0)" class="login-info">
	  		<table>
  				<tr>
  					<td>用户名 <span class="registerVerify" :style="registerVerify.bol?'background:green':'background:red'">{{registerVerify}}</span></td>
  					<td><input type="text" placeholder="请输入用户名" @blur="registerVerifyFn()" ref="registerUsername"></td>
	  			</tr>
	  			<tr>
	  				<td>邮箱</td>
	  				<td><input type="text" placeholder="请输入邮箱" ref="registerEmail"></td>
	  			</tr>
	  			<tr>
	  				<td>密码</td>
	  				<td><input type="password" placeholder="请输入密码" ref="registerPassword"></td>
	  			</tr>
	  			<tr>
	  				<td>确认密码</td>
	  				<td><input type="password" placeholder="请确认密码"></td>
	  			</tr>
	  			<tr><td colspan='2'><input type="submit" @click="registerFn()"></td></tr>
	  		</table>
	  	</form>
	  </mt-tab-container-item>
	</mt-tab-container>
  </div>	
</template>

<script>
import { mapGetters, mapActions } from 'vuex';
import commonHeader from './commonHeader'
export default {

  data () {
    return {
     	selected:"1",
     	registerVerify:""
    }
  },
  computed:{
  	...mapGetters(
  		{getLoginInfoArr:'getLoginInfoArr'}
  		),
  },
  methods:{
  	...mapActions([
  			'loginVerify',
  			'loginRegister',
  			'loginRegisterEixt',
  			'setLoginFn'
  		]),
  	regPasswordVerifyFn:function(){
  		// var reg=/\^[]/
  	},
  	loginFn:function(){
  		// var reg=/(?=.*[0-9])(?=.*[a-zA-Z])(?=.*[^a-zA-Z0-9])([\u4e00-\u9fa5]).{2,7}/;
  		// if(!(reg.test(this.$refs.loginPassword.value))){
  		// 	alert('错误的用户名格式')
  		// 	return;
  		// }
  		var that = this;
  		this.loginVerify({username:this.$refs.loginUsername.value,
  			password:this.$refs.loginPassword.value}).then(function(res){
  				if(res.password){
		  			alert("登录成功");
		  			var str=JSON.stringify({bol:1,username:that.$refs.loginUsername.value,password:that.$refs.loginPassword.value})
					localStorage.setItem("meituanUser",str);
					// that.setLoginFn({username:that.$refs.loginUsername.value})
		  			that.$router.push('/my');
		  		}else{
		  			if(res.password==false){
		  				alert("密码错误");
		  			}else if (res.username==false) {
		  				alert("用户未注册")
		  			}else{
		  				alert("信息错误，请重试")
		  			}
		  			that.$refs.loginPassword.value=""
		  			localStorage.setItem("meituanUser",{bol:0,username:null,password:null});
		  		}
  		})
  		
  		
  	},
	registerVerifyFn:function(){
		var that = this;
		this.loginRegisterEixt({username:event.currentTarget.value}).then(function(res){
			that.registerVerify=res.mes;
		});
	},
	registerFn:function(){
		// var reg=/(?=.*[0-9])(?=.*[a-zA-Z])(?=.*[^a-zA-Z0-9]).{8,30}/;
  // 		if(!(reg.test(this.$refs.loginPassword.value))){
  // 			alert('错误的用户名格式')
  // 			return;
  // 		}
		var that = this;
		var bol=this.loginRegister({username:this.$refs.registerUsername.value,password:this.$refs.registerPassword.value,email:this.$refs.registerEmail.value})
		bol.then(function(res){
			if(res.usernameExit){
				alert('用户名已被使用')
				console.log(getLoginInfoArr)
			}else{
				console.log(getLoginInfoArr)
			}
		})
	}
  },
  components:{
    'commonHeader':commonHeader	
  },
  mounted:function(){
 		
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="stylus" scoped>
@import "../../assets/common/stylus/common.styl";
.login
	background white
.login-header
	height pxtorem(86px)
	text-align center
	line-height pxtorem(86px)
	font-size pxtorem(30px)
	font-weight 900
.login-title
	font-size pxtorem(26px)
.login-info
	background white
	font-size pxtorem(32px)
	tr
		height pxtorem(96px)
		td:first-child
			padding-left pxtorem(20px)
	tr:last-of-type
		text-align center
		padding-top pxtorem(60px)
		height pxtorem(200px)
		td:first-child
			padding-left 0
	td
		width pxtorem(375px)
		input
			border none
			outline none
		input[type=text],input[type=password]
			height pxtorem(50px)
			font-size pxtorem(30px)
		input[type=submit]
			width 100%
			height pxtorem(80px)
			background #26a2ff
			color white
			font-size pxtorem(30px)
.login-tab-container
	margin-top pxtorem(60px)
.registerVerify
	font-size pxtorem(22px)	
	color white


</style>



