export default {
	getIgBol (state) {
		return state.igbol;
	},
	getCity (state) {
		return state.city;
	},
	getNum (state) {
		return state.num;
	}, 
	getMovieCommit (state) {
		return state.movieCommit;
	},
	getPeople_Commit (state) {
		return state.people_commit;
	},
	getSent (state) {
		return state.sent;
	},
	getimagedata (state) {
		return state.imgadata;
	},
	getLoginInfoArr(state){
		return state.loginInfoArr;
	},
	getLoginInfoPass(state){
		return state.loginInfoPass;
	},
	getUserLocation(state){
		return state.userLocation
	},
	getAddressArr(state){
		return state.addressArr;
	}
}; 


