<template>
 <div class="people">
 	<div class="header">
 		<img src="../../../assets/image/movie/back.png" @click='backFn'>
 		<p>{{commit.name}}的影评</p>
 	</div>
 	<div class="content">
 		<img class="left" :src="commit.url" :onerror="ess">
 		<div class="right">
 			<p>{{commit.name}}</p>
 			<p>
 				<img src="../../../assets/image/movie/star.png" v-for="item in num">
                <img src="../../../assets/image/movie/star2.png" v-for="item in nums">
                <img src="../../../assets/image/movie/star1.png" v-for="item in num1">
 			</p>
 			<p>{{commit.conten}}</p>
 			<div class="footer">
 				<span class="sp">{{commit.time}}</span>
 				<div class="zan">
 					<img @click="ZanFn" :src="zanbol? url[0]:url[1]">
	 				<span>{{commit.zan}}</span>
	 				<img src="../../../assets/image/movie/iconfont2.png">
 				</div>
 			</div>
 		</div>
 	</div>
 	<p class="reler">{{mess}} 条回复</p>
 	<ul>
	 	<li v-for='(item,index) in sentArr'>
	 		<img class="left" src="../../../assets/image/duoduo.png">
	 		<div class="right">
	 			<p>{{item.name}}</p>
	 			<p></p>
	 			<p>{{item.content}}</p>
	 			<div class="footer">
	 				<span class="sp">{{item.time}}</span>
	 				<div class="zan">
	 					<img @click="goodFn(index)" :src="sentbol[index] ? url[0]:url[1]">
		 				<span>{{numArr[index]}}</span>
	 				</div>
	 			</div>
	 		</div>
	 	</li>	
 	</ul>
 	<div class="fixeds">
 		<input type="text" v-model='mes'>
 		<span @click='clickFn'>发送</span>
 	</div>
 </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';

export default {
  data () {
    return {
     num:0,
     nums:0,
     num1:0,
     rely:0,
     mes:'',
     sentbol:[],
     url:[require("../../../assets/image/movie/good2.png"),require("../../../assets/image/movie/good.png")],
     numArr:[],
     zanbol:false,
     mess:0,
     ess:'this.src="'+require('../../../assets/image/nav/duoduo.png')+'"'
    }
  },
  created(){
  	console.log(this.commit);
  	this.star();
  },
  methods:{
  	...mapActions([
        'setSent'
    ]),
  	star:function (){
        var s = this.commit.scor;
        this.num=parseInt(s);
        var tt=s-this.num;
        if(tt>=0.5 && tt<=0.99){
         this.nums=1;
        }else{
         this.nums=0;
        }
        this.num1=5-this.num-this.nums;
      },
  	backFn:function(){
  		this.$router.go(-1);
  	},
  	ZanFn:function(){
  		// 点赞
  		if(!this.zanbol){
  			this.commit.zan++;
  		}else{
  			this.commit.zan--;
  		}
  		this.zanbol=!this.zanbol;
  	},
  	goodFn:function(index){
  		// 个人点赞
  		var bol=!this.sentbol[index];
  		this.$set(this.sentbol,index,bol);
  		// 点赞加一
  		var arrnum=this.numArr;
  		if(bol){
  			arrnum[index]++;
  		}else{
  			arrnum[index]--;
  		}
  		this.numArr=arrnum;
  		
  		// console.log(arrnum)
  	},
  	clickFn:function(){
  		// 评论
  		var now = new Date();  
        var year = now.getFullYear();       //年  
        var month = now.getMonth() + 1;     //月  
        var day = now.getDate();            //日  
        var hh = now.getHours();            //时  
        var mm = now.getMinutes();          //分  
        var str={'name':'番薯','approve':0,'content':this.mes,'time':year+"-"+month+"-"+day+' '+hh+":"+mm}
		var arr=this.sentArr||[];
		arr.push(str);
		this.sentArr=arr;
		this.mes='';
		this.setSent(arr);
		// 个人点赞遍历下标
  		var arrbol=this.sentbol;
  			arrbol.push(false);
  		this.sentbol=arrbol;
  		// 点赞加1
  		this.numArr.push(0);
  		this.numArr=this.numArr;
  		this.mess++;
  	}
  },
  computed: mapGetters({
  	commit:'getPeople_Commit',
  	sentArr:'getSent'
  })
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='stylus' scoped>
@import "../../../assets/common/stylus/common.styl"
.people
	.header
		position fixed
		top 0
		left 0
		width 100%
		height pxtorem(69px)
		padding-top pxtorem(35px)
		background white
		border-bottom pxtorem(1px) solid #ccc
		img
			width pxtorem(46px)
			height pxtorem(40px)
			float left
			margin-left pxtorem(35px)
		p
			font-size pxtorem(36px)
			float left
			margin-top pxtorem(4px)
			margin-left pxtorem(47px)
	.content
		padding-top pxtorem(141px)
		padding-bottom pxtorem(42px)
		background white
		overflow hidden
		border-bottom pxtorem(1px) solid #ccc
		.left
			width pxtorem(70px)
			height pxtorem(70px)
			float left
			border-radius 50%
			margin-left pxtorem(30px)
		.right
			float left
			width pxtorem(590px)
			margin-left pxtorem(23px)
			margin-top pxtorem(14px)
			p:nth-child(1)
				font-size pxtorem(33px)
				margin-bottom pxtorem(30px)
			p:nth-child(3)
				font-size pxtorem(32px)
				margin-top pxtorem(30px)
			p:nth-child(2)
				img
					width pxtorem(26px)
					height pxtorem(26px)
			.footer
				overflow hidden
				margin-top pxtorem(43px)
				.sp
					float left
					font-size pxtorem(28px)
				.zan
					display inline-block
					float right
					img:nth-child(1)
						width pxtorem(32px)
						height pxtorem(37px)
					img:nth-child(3)
						width pxtorem(40px)
						height pxtorem(37px)
					span
						font-size pxtorem(28px)
						vertical-align top
						display inline-block
						margin-top pxtorem(3px)
						margin-right pxtorem(20px)
	ul
		padding-top pxtorem(70px)
		padding-bottom pxtorem(123px)
		li
			overflow hidden
			margin-bottom pxtorem(30px)
			.left
				width pxtorem(70px)
				height pxtorem(70px)
				float left
				border-radius 50%
				margin-left pxtorem(30px)
			.right
				float left
				width pxtorem(590px)
				margin-left pxtorem(23px)
				margin-top pxtorem(14px)
				padding-bottom pxtorem(30px)
				border-bottom pxtorem(1px) solid gray
				p:nth-child(1)
					font-size pxtorem(33px)
					margin-bottom pxtorem(30px)
				p:nth-child(3)
					font-size pxtorem(32px)
					margin-top pxtorem(30px)
				.footer
					overflow hidden
					margin-top pxtorem(43px)
					.sp
						float left
						font-size pxtorem(28px)
					.zan
						display inline-block
						float right
						img:nth-child(1)
							width pxtorem(32px)
							height pxtorem(37px)
						span
							font-size pxtorem(28px)
							vertical-align top
							display inline-block
							margin-top pxtorem(3px)
							margin-right pxtorem(20px)
	.reler
		font-size pxtorem(31px)
		margin-left pxtorem(30px)
		margin-top pxtorem(30px)
	.fixeds
		height pxtorem(120px)
		overflow hidden
		position fixed
		bottom 0
		left 0
		width 100%
		background white
		input
			width pxtorem(590px)
			height pxtorem(71px)
			float left
			margin-left pxtorem(24px)
			margin-top pxtorem(20px)
			border pxtorem(1px) solid gray
			border-radius pxtorem(12px)
			font-size pxtorem(30px)
			text-indent pxtorem(16px)
		span
			font-size pxtorem(32px)
			float left
			margin-top pxtorem(41px)
			margin-left pxtorem(30px)



</style>



