<template>
  <div class="personalInfo">
  	<commonHeader title="我的账户"></commonHeader>
  	<ul>
  		<li class="clear"><div class="fl abc">账户名字</div><div class="fr">{{getLoginInfoPass.username}}</div></li>
  		<router-link tag="li" to="/initPassword" class="clear"><div class="fl">登录密码</div><div class="fr">设置 &nbsp &nbsp ></div></router-link>
  	</ul>
  	<span @click="exitFn">退出当前账号</span>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';
import commonHeader from './commonHeader'
export default {
  data () {
    return {
     
    }
  },
  components:{
    'commonHeader':commonHeader	
  },
  computed:{
    ...mapGetters(
      {getLoginInfoArr:'getLoginInfoArr',
       getLoginInfoPass:'getLoginInfoPass' 
      }
      )
  },
  methods:{
  	  ...mapActions({
    	setLoginFn:'setLoginFn'
    }),
  	exitFn:function(){
  		this.setLoginFn({})
  		localStorage.setItem("meituanUser",0);
  		console.log(localStorage.getItem("meituanUser"),this.getLoginInfoPass)
  	}
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="stylus" scoped>
@import "../../assets/common/stylus/common.styl";
ul
	padding-top pxtorem(20px)
	li
		height pxtorem(90px)
		line-height pxtorem(90px)
		border-bottom 1px solid #ccc
		background white
		>div:first-child
			margin-left pxtorem(20px)
		>div:last-child
			margin-right pxtorem(20px)
span
	position fixed
	display block
	width pxtorem(750px)
	height pxtorem(100px)
	line-height pxtorem(100px)
	text-align center
	bottom 0
	left 0
	color red
	background white

</style>



