import axios from 'axios';
import localEvent from './local'

export default {
	setIgBol({commit, state},data){
		// 直接调mutations的方法
		commit('IG_BOL',data);
	},
	setMovieCommit({commit, state},data){
		// 直接调mutations的方法
		commit('MOVIE_COMMIT',data);
	},
	setPeple_Commit({commit, state},data){
		// 直接调mutations的方法
		commit('PEOPLE_COMMIT',data);
	},
	setSent({commit, state},data){
		// 直接调mutations的方法
		commit('SENT',data);
	},
	setCity({commit, state},data){
		// 直接调mutations的方法
		commit('CITY',data);
	},
	setNum({commit, state},data){
		// 直接调mutations的方法
		commit('NUM',data);
	},
	setimagedata({commit, state},data){
		// 直接调mutations的方法
		commit('IMAGEDATE',data);
	},
	loginVerify({commit,state},data){
		console.log(state.loginInfoArr)
		for(var i=0;i<state.loginInfoArr.length;i++){
			if(data.username==state.loginInfoArr[i].username){
				if(data.password==state.loginInfoArr[i].password){
					commit('loginFn',data)
					return {password:true}
				}else{
					return {password:false}
				}
			}
		}
		return {username:false}
	},
	loginRegister({commit,state},data){
		for(var i=0;i<state.loginInfoArr.length;i++){
			if(data.username==state.loginInfoArr[i].username){
				return {usernameExit:true}
			}
		}
		data.id=state.loginInfoArr.length;
		commit('insertLoginInfo',data);
		return {usernameExit:false}
	},
	loginRegisterEixt({commit,state},data){
		if(data.username==""){
			return {mes:'用户名不能为空',bol:false}
		}
		for(var i=0;i<state.loginInfoArr.length;i++){
			if(data.username==state.loginInfoArr[i].username){
				return {mes:'该用户名已被使用',bol:false}
			}
		}
		return {mes:'未被使用的用户名',bol:true}
	},
	setLoginFn({commit,state},data){
		commit('loginFn',data)
	},
	updatePassword({commit,state},data){
		var data=data;
		for(var i=0;i<state.loginInfoArr.length;i++){
			if(data.username==state.loginInfoArr[i].username){
				if(data.password==state.loginInfoArr[i].password){
					data.index=i;
					console.log(i)
					
				}else{
					return {bol:0}
				}
			}
		}
		commit('updatePasswordFn',data)
		return {bol:1}
	},
	setUserLocation({commit,state},data){
		commit('setUserLocationFn',data)
	},
	setAddressArr({commit,state},data){
		data.id=state.addressArr.length;
		commit('setAddressArrFn',data)
	}
};


